.AdminButtonsContainer {
  margin-top: 1rem;
  width: 100%;
  max-width: 400px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
}

.AdminButton {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 13px;
  transition: background-color 0.2s;
  
  &:hover {
    background-color: #218838;
  }
}

.AdminButtonDanger {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 13px;
  transition: background-color 0.2s;
  
  &:hover {
    background-color: #c82333;
  }
}