.ChessboardWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  position: relative;
  aspect-ratio: 1;

}

.ChessboardWrapper > div {
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  width: 100% !important;
  height: 100% !important;
  border-radius: 8px 8px 0 0;
  border: none;
}

.ChessboardWrapperIntegrated {
  border-bottom: none;
  overflow: hidden;
}

.ChessboardWrapperIntegrated > div {
  border: none;
  border-radius: 0;
}

.ChessboardWrapper > div > div {
  width: 100% !important;
  height: 100% !important;
}

