.ModalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.ModalContainer {
  background: white;
  border-radius: 8px;
  max-width: 400px;
  width: 90%;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.ModalHeader {
  padding: 20px 24px 0;
  border-bottom: 1px solid #e1e5e9;

  h3 {
    margin: 0 0 16px 0;
    font-size: 18px;
    font-weight: 600;
    color: #e53e3e;
  }
}

.ModalContent {
  padding: 20px 24px;
  text-align: center;
}

.WarningIcon {
  font-size: 48px;
  margin-bottom: 16px;
}

.WarningText {
  margin-bottom: 20px;

  p {
    margin: 0 0 8px 0;
    color: #4a5568;
    
    &:last-child {
      margin-bottom: 0;
    }

    strong {
      color: #e53e3e;
    }
  }
}

.PuzzleInfo {
  background: #f7fafc;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  padding: 16px;
  margin-bottom: 16px;
  text-align: left;
}

.InfoRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;

  &:last-child {
    margin-bottom: 0;
  }
}

.Label {
  font-weight: 500;
  color: #4a5568;
}

.Value {
  font-weight: 600;
  color: #2d3748;
}

.ErrorMessage {
  background: #fed7d7;
  border: 1px solid #fc8181;
  border-radius: 6px;
  padding: 12px;
  margin-top: 16px;
  color: #c53030;
  font-size: 14px;
  text-align: left;
}

.ModalActions {
  padding: 16px 24px 24px;
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  border-top: 1px solid #e1e5e9;
}

.Button {
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;

  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
}

.CancelButton {
  background: #f7fafc;
  color: #4a5568;
  border: 1px solid #e2e8f0;

  &:hover:not(:disabled) {
    background: #edf2f7;
  }
}

.DeleteButton {
  background: #e53e3e;
  color: white;

  &:hover:not(:disabled) {
    background: #c53030;
  }
}