.ModalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.ModalContainer {
  background: white;
  border-radius: 8px;
  max-width: 400px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.ModalHeader {
  padding: 20px 24px 0;
  border-bottom: 1px solid #e1e5e9;

  h3 {
    margin: 0 0 16px 0;
    font-size: 18px;
    font-weight: 600;
    color: #2d3748;
  }
}

.ModalContent {
  padding: 20px 24px;
}

.MovesSelection {
  margin-bottom: 20px;

  h4 {
    margin: 0 0 12px 0;
    font-size: 16px;
    font-weight: 600;
    color: #2d3748;
  }
}

.MovesGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
  gap: 6px;
  margin-bottom: 16px;
}

.MoveButton {
  display: flex;

  align-items: center;
  padding: 8px 4px;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  background: white;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  

  &:hover {
    background: #f7fafc;
    border-color: #cbd5e0;
  }
}

.MoveButtonSelected {
  background: #3182ce;
  border-color: #2c5282;
  color: white;

  &:hover {
    background: #2c5282;
  }
}

.MoveNumber {
  font-size: 11px;
  font-weight: 600;
  opacity: 0.7;
}

.MoveNotation {
  font-family: 'Courier New', monospace;
  font-size: 13px;
  font-weight: 600;
}


.ErrorMessage {
  background: #fed7d7;
  border: 1px solid #fc8181;
  border-radius: 6px;
  padding: 12px;
  margin-top: 16px;
  color: #c53030;
  font-size: 14px;
}

.ModalActions {
  padding: 16px 24px 24px;
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  border-top: 1px solid #e1e5e9;
}

.Button {
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;

  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
}

.CancelButton {
  background: #f7fafc;
  color: #4a5568;
  border: 1px solid #e2e8f0;

  &:hover:not(:disabled) {
    background: #edf2f7;
  }
}

.ConfirmButton {
  background: #3182ce;
  color: white;

  &:hover:not(:disabled) {
    background: #2c5282;
  }
}